export function formatDate(date, fmt) {
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    let o = {
      'M+': date.getMonth() + 1,
      'd+': date.getDate(),
      'h+': date.getHours(),
      'm+': date.getMinutes(),
      's+': date.getSeconds()
    }
    for (let k in o) {
      if (new RegExp(`(${k})`).test(fmt)) {
        let str = o[k] + ''
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : padLeftZero(str))
      }
    }
    return fmt
  }

  export function formatSize(limit, fixed) {
    if(fixed==null) fixed = 2
    var size = "";
    if(limit < 1024){                            //0.1KBB
        size = limit.toFixed(fixed) + " B"
    }else if(limit < 1024 * 1024){            //0.1MBKB
        size = (limit/1024).toFixed(fixed) + " KB"
    }else if(limit < 1024 * 1024 * 1024){        //0.1GBMB
        size = (limit/(1024 * 1024)).toFixed(fixed) + " MB"
    }else{                                            //GB
        size = (limit/(1024 * 1024 * 1024)).toFixed(fixed) + "GB"
    }
 
    var sizeStr = size + "";                        //
    var index = sizeStr.indexOf(".");                    //
    var dou = sizeStr.substr(index + 1 ,2)            //
    if(dou == "00"){                                //0000               
        return sizeStr.substring(0, index) + sizeStr.substr(index + 3, 2)
    }
    return size;
}
   
  function padLeftZero(str) {
    return ('00' + str).substr(str.length)
  }